











import { EventTreeNode } from '@/models/EventTreeNode';
import { Getters } from '@/store/getters';
import Vue from 'vue';
import { mapGetters } from 'vuex';

export default Vue.extend({
  props: {
    node: {
      type: Object as () => EventTreeNode,
    },
    maxArcWidth: Number,
  },

  data() {
    return {
      value: this.node,
    };
  },

  computed: {
    ...mapGetters({
      getNodeScale: Getters.GET_NODE_SCALE,
    }),

    radius(): number {
      return this.getNodeScale(this.node.count) * 1.25;
    },

    newX(): number {
      const parent = this.node.parents[0];
      return parent.x + this.maxArcWidth + (this.getNodeScale(this.node.count) / 2);
    },

    newY(): number {
      const parent = this.node.parents[0];
      return parent.y + this.maxArcWidth + this.getNodeScale(parent.count) * 1.25;
    },
  },
});
